.App {
  text-align: center;
  background-color: #121212;
  width: 100vw;
  height: 100vh;
  /* display: flex; */
  color: white;
}

h2,
h1 {
  font-family: Helvetica, Arial, sans-serif;
  color: white;
}

body {
  padding: 0%;
  margin: 0;
}

nav {
  height: 60px;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid grey;
  display: grid;
  place-items: center;
}

nav h1 {
  margin: 0;
  font-family: Helvetica, Arial, sans-serif;
  color: white;
  font-size: 45px;
}

.alert {
  top: 6%;
  right: 30%;
  width: 40%;
  display: block;
  position: absolute;
  border-radius: 5px;
  background-color: #3a393c;
}

.game {
  width: 100vw;
  height: calc(100vh - 170px);
  display: flex;
  align-items: center;
  padding-top: 50px;
  flex-direction: column;
}

.board {
  width: 450px;
  height: 550px;
  display: flex;
  flex-direction: column;
}

.row {
  flex: 33%;
  display: flex;
  flex-direction: row;
  margin: 5px;
}

.letter {
  flex: 33%;
  height: 100%;
  border: 1px solid grey;
  margin: 5px;
  display: grid;
  place-items: center;
  font-size: 30px;
  font-weight: bolder;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}

.keyboard {
  width: 700px;
  height: 300px;
  margin-top: 60px;
}

.line1 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin: 5px;
}
.line2 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}
.line3 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}

.key {
  width: 50px;
  height: 70px;
  margin: 5px;
  border-radius: 4px;
  display: grid;
  place-items: center;
  font-size: 20px;
  background-color: grey;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
}

#big {
  width: 100px;
}

/* Key Custom Color */

#disabled {
  background-color: #3a393c;
}

#right {
  background-color: #528d4e;
}

#almost {
  background-color: #b49f39;
}

/* Letters overlay colors */

.error {
  background-color: #3a393c !important;
  border: none !important;
  animation: 0.5s linear flipAnimation;
}

.almost {
  background-color: #b49f39 !important;
  border: none !important;
  animation: 0.5s linear flipAnimation;
}

.correct {
  background-color: #528d4e !important;
  border: none !important;
  animation: 0.5s linear flipAnimation;
}

/* end game msg */

#lost {
  color: #b60000;
}

#win {
  color: #00b600;
}

.flip {
  animation: 0.5s linear flipAnimation;
}

@keyframes flipAnimation {
  0% {
    transform: rotateX(0deg);
  }

  50% {
    transform: rotateX(90deg);
  }

  100% {
    transform: rotateX(0deg);
  }
}
